import { Component, OnInit } from '@angular/core';
import { SocketServiceService } from '../services/socket/socket-service.service';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import * as dat from 'dat.gui';
import * as CANNON from 'cannon-es';
import gsap from 'gsap';


@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.css']
})
export class PortfolioComponent implements OnInit {

  flag: boolean = false;
  models: any=[];

  constructor(private socketService: SocketServiceService) { }

  ngOnInit(): void {

    // Listen to events from socket-io server
    this.socketService.listen('test event').subscribe((data) => {
      console.log(data);
    });

    // Sending out my existense to server
    this.socketService.emit('send message','Hello every body');

    // Server sending back all the user existence
    this.socketService.listen('sendtoall').subscribe((data) => {
      // playJoiningSound();
    })

    // Disconnection status
    this.socketService.listen('disconnectionstatus').subscribe((data) => {
      playDisconnectedSound();
    })

    //Canvas
    const canvas = document.querySelector('.webgl')

    //Scene Initialisation  
    const scene = new THREE.Scene()

    const fog = new THREE.Fog('#262837', 1, 15)
    // scene.fog = fog

    // Sounds
    const userJoiningSound = new Audio('/assets/sounds/joined.mp3')

    const playJoiningSound = () =>
    {
      userJoiningSound.currentTime = 0
      userJoiningSound.play()
    }

    const userDisconnectedSound = new Audio('/assets/sounds/disconnected.mp3')
    const playDisconnectedSound = () =>
    {
      userDisconnectedSound.currentTime = 0
      userDisconnectedSound.play()
    }

    // Texture Loader
    const textureLoader = new THREE.TextureLoader()

    // Loaders and decoders

    const dracoLoader = new DRACOLoader()
    dracoLoader.setDecoderPath('assets/decoders/draco/')

    const gltfLoader = new GLTFLoader()
    gltfLoader.setDRACOLoader(dracoLoader)

    // Textures
    const bakedTexture = textureLoader.load('assets/models/2/baked.jpg')
    bakedTexture.flipY = false
    bakedTexture.encoding = THREE.sRGBEncoding

    // Material

    //Baked Material
    const bakedMaterial = new THREE.MeshBasicMaterial({ map: bakedTexture })

    // Model
    gltfLoader.load(
      'assets/models/2/model.glb',
      (gltf) => {
          
          const mergedMesh = gltf.scene.children.find((child) => child.name === 'baked')
          if(mergedMesh instanceof THREE.Mesh) {
            mergedMesh.material = bakedMaterial
          }
          
          scene.add(gltf.scene)
          gltf.scene.receiveShadow = true
          this.models.push(gltf.scene)
      }
    )


    // Font Loader
    const fontLoader = new THREE.FontLoader()
    fontLoader.load(
        'assets/fonts/helvetiker_regular.typeface.json',
        (font) => {
            const textGeometry = new THREE.TextBufferGeometry(
                'UST',
                {
                    font: font,
                    size: .5,
                    height: 0.05,
                    curveSegments: 12,
                    bevelEnabled: true,
                    bevelThickness: 0.03,
                    bevelSize: 0.02,
                    bevelOffset: 0,
                    bevelSegments: 5
                }
            )
            //Make the text center
            textGeometry.computeBoundingBox()
            textGeometry.center()

            const textMaterial = new THREE.MeshBasicMaterial({
              color: 0xffffff
            })
            const text = new THREE.Mesh(textGeometry, textMaterial)
            text.rotation.y = Math.PI / 2
            text.position.x = -3.60
            // text.position.z = - 3
            text.position.y = 3.2
            // scene.add(text)
            text.castShadow = true
            text.receiveShadow = true
        }
    )

    //Renderer Size
    const sizes = {
      width: innerWidth,
      height: innerHeight
    }

    //Render Initialisation
    const renderer = new THREE.WebGLRenderer()
    document.body.appendChild( renderer.domElement );
    renderer.shadowMap.enabled = true
    renderer.shadowMap.type = THREE.PCFSoftShadowMap
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    renderer.outputEncoding = THREE.sRGBEncoding
    renderer.setClearColor('#ffffff')

    //Camera Initialisation
    const camera = new THREE.PerspectiveCamera(75,  sizes.width / sizes.height, 0.1, 10000)
    camera.position.x = 0
    camera.position.y = 5
    camera.position.z = 12

    camera.lookAt(new THREE.Vector3(0,0,0));
    scene.add(camera)

    //Controls
    const controls = new OrbitControls(camera, renderer.domElement )
    controls.enableDamping = true;

    //Resize Event
    window.addEventListener('resize', () =>
    {
        // Update sizes
        sizes.width = window.innerWidth
        sizes.height = window.innerHeight

        //Update camera
        camera.aspect = sizes.width / sizes.height
        camera.updateProjectionMatrix()

        //Update renderer
        renderer.setSize(sizes.width, sizes.height)
        renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    })

    // Clock
    let clock = new THREE.Clock()
    let oldELapsedTime = 0

    //Animate
    const tick = () => {

      //Clock
      const elapsedTime = clock.getElapsedTime()
      const deltaTime = elapsedTime - oldELapsedTime
      oldELapsedTime = elapsedTime
      
      controls.update()

      if(this.models) {
        this.models.forEach((model :any) => {
          model.rotation.y += 0.002
        });
      }

      //Rendering Section
      renderer.render(scene, camera)

      window.requestAnimationFrame(tick)

    }

    tick()

  }

}